}
header .navigation li:hover{
  background: rgba(0, 139, 139, 0.74);
  cursor: pointer;
  border-radius: 6px;
}
header .navigation li a {
  color:rgb(255, 255, 255);
  text-decoration: none;
  font-weight: 400;
  border-radius: 3px;
}
.obsv-image{
  height: 300px;
  width: 600px;
}
@media screen and (max-width: 1200px) {
  nav{
      margin: 24px 20px;
  }
  header {
      height: 10vh;
      width: 100%;
      background: rgb(0, 52, 65);
  }
  header .logo {
      height: 35px;
      width: 35px;
      border-radius: 50%;
      margin-left: 1%;
      margin-right: 1%;
  }
  header div{
      display: flex;
      flex-direction: row;
      /* width: 100%; */
      justify-content: center;
      /* gap: 1rem; */
  }
  header div h1 {
