      flex-direction: row;
      gap: 1rem;
  }
  header{
      display: flex;
      flex-direction: column;
  }
  header .navigation li {
      font-size: 12px;
      list-style: none;
      margin-left: 20px;
      padding: 8px 1px;
  }
  header .navigation li:hover{
      background: rgba(0, 139, 139, 0.74);
      cursor: pointer;
      border-radius: 6px;
  }
  header .navigation li a {
      color:rgb(255, 255, 255);
      text-decoration: none;
      font-weight: 400;
      border-radius: 3px;
  }
  .obsv-image{
      height: 300px;
      width: 600px;
  }
  .language-cpp{
      height: auto;
      width: 80vw;
  }
  .mth{
     font-size: 1rem;
     width: 40%;
  }
  .instruc, .detl, .detl-1,.obs {
      text-align: center;
      font-size: 0.8rem;
      /* margin: 1.5%; */
  }
  .detl {
      font-size: 0.8rem;
  }
  .methods{
      font-size: 0.6rem;
  }
}
@media screen and (max-width: 480px) {
  header div{
      width: 45%;
  }
  header div h1{
      font-size: 0.8rem;
  }
  header .navigation li{
      font-size: 0.6rem;
  }
  .mth{
      font-size: 0.6rem;
      width: 60%;
  }
  .desc-heading{
      width: 100%;
  }
  .instruc, .detl, .detl-1,.obs {
      text-align: center;
      font-size: 0.8rem;
  }
